import { DeepPartial } from '../core'
import { Chain, ChainRpc } from './chain.model'

/**
 * Chain Mock
 */
export function ChainMock(options?: DeepPartial<Chain>): Chain {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'CHAIN_ID',
    chainID: options?.chainID || 0,

    name: options?.name || 'CHAIN_NAME',
    hex: options?.hex || 'CHAIN_HEX',
    blocksPerYear: options?.blocksPerYear || 0,
    tokenSymbol: options?.tokenSymbol,
    RPCs: options?.RPCs ? options?.RPCs?.map((rpc) => ChainRPCMock(rpc)) : [],

    createdAt: options?.createdAt || now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: options?.updatedAt || now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function ChainRPCMock(options?: Partial<ChainRpc>): ChainRpc {
  return {
    name: options?.name || 'RPC_NAME',
    https: options?.https || 'RPC_HTTPS',
    wss: options?.wss || 'RPC_WSS',
    provider: options?.provider || 'ALCHEMY',
    isPublic: options?.isPublic,
  }
}
